.psh__history-data{
    margin-top: .3rem;
    justify-content: start;
    text-align: start;
    padding: 1rem 0 0 1rem;
}

.psh__history-data > *{
    padding-bottom: 1rem;
}

.psh__history-rounder{
    display: inline-block;
    width: 13px;
    height: 13px;
    background-color: black;
    border-radius: 50%;
}

.psh__history-line{
    display: block;
    width: 1px;
    height: 100%;
    background-color: black;
    transform: translate(6px, -7px);
}